* {
    box-sizing: border-box;
    margin: 0;
  }
  
  html,
  body,
  #root {
    margin: 0;
    padding: 0;
    background-color: white;
  }
  
  body {
    font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir, helvetica neue, helvetica, ubuntu, roboto, noto, segoe ui, arial,
      sans-serif;
  }
 

  .padding-block {
    display: flex;
    text-align: center;
    font-size: 5vmax;
    justify-content: center;
    align-items: center;
    background-color: #efefef;
    color: #333;
  }
  .padding-block-below {
    display: flex;
    text-align: center;
    font-size: 5vmax;
    justify-content: center;
    align-items: center;
    background-color: #efefef;
    color: #333;
  }

  .normal-text{
      font-weight: 400;
      padding-top: 10%;
      font-size: 30%;
      font-family: Poppins;
  }
  .seventy{
    font-size: 70%; 
  }
  .fifty{
    font-size: 50%; 
  }
  .scroll-carousel {
    background-color: #fff;
  }
  
  .sticky-box {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    overflow: hidden;
  }
  
  .transform-box {
    display: flex;
    will-change: transform;
  }
  
  .box {
    flex-shrink: 0;
    /* need to hardcode box sizes, else images loading throws off calculations */
    /* could be a job for imagesLoaded */
    width: 70rem;
    height: 100vh;
    margin: 0 2rem;
  }
  
  .box--narrower {
    width: 20rem;
  }
  
  .img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, font, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, caption {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    font-size: 100%;
    vertical-align: baseline;
    background: transparent;
}

